'use client'

import { useEffect, useMemo, useState } from 'react'
import Link from 'next/link'
import { useRouter } from 'next/navigation'
import { createClient } from '@/lib/supabase/client'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Label } from '@/components/ui/label'
import { Input } from '@/components/ui/input'
import { Button } from '@/components/ui/button'
import { Switch } from '@/components/ui/switch'

export const dynamic = 'force-dynamic'

interface Preferences {
  email_updates: boolean
  class_reminders: boolean
}

export default function SettingsPage() {
  const [profile, setProfile] = useState<any>(null)
  const [loading, setLoading] = useState(true)
  const [savingProfile, setSavingProfile] = useState(false)
  const [savingPassword, setSavingPassword] = useState(false)
  const [savingPrefs, setSavingPrefs] = useState(false)
  const [form, setForm] = useState({ full_name: '', email: '' })
  const [passwords, setPasswords] = useState({ newPassword: '', confirmPassword: '' })
  const [preferences, setPreferences] = useState<Preferences>({ email_updates: true, class_reminders: true })
  const router = useRouter()

  const prefsStorageKey = useMemo(() => (profile ? `acl:preferences:${profile.id}` : ''), [profile])

  useEffect(() => {
    async function load() {
      const supabase = createClient()
      const {
        data: { user },
      } = await supabase.auth.getUser()

      if (!user) {
        router.push('/auth/login')
        return
      }

      const { data } = await supabase
        .from('profiles')
        .select('id, role, full_name, email, school_id')
        .eq('id', user.id)
        .single()

      setProfile(data)
      setForm({ full_name: data?.full_name || '', email: data?.email || '' })
      setLoading(false)
    }

    load()
  }, [router])

  useEffect(() => {
    if (!prefsStorageKey) return
    const raw = localStorage.getItem(prefsStorageKey)
    if (!raw) return
    try {
      const parsed = JSON.parse(raw)
      setPreferences({
        email_updates: parsed.email_updates ?? true,
        class_reminders: parsed.class_reminders ?? true,
      })
    } catch {
      // ignore malformed local pref payload
    }
  }, [prefsStorageKey])

  async function saveProfile() {
    if (!profile) return
    setSavingProfile(true)

    const supabase = createClient()
    const { error } = await supabase
      .from('profiles')
      .update({ full_name: form.full_name })
      .eq('id', profile.id)

    if (error) {
      alert(error.message)
    }

    setSavingProfile(false)
  }

  async function updatePassword() {
    if (!passwords.newPassword || passwords.newPassword.length < 8) {
      alert('Password must be at least 8 characters.')
      return
    }
    if (passwords.newPassword !== passwords.confirmPassword) {
      alert('Passwords do not match.')
      return
    }

    setSavingPassword(true)
    const supabase = createClient()
    const { error } = await supabase.auth.updateUser({ password: passwords.newPassword })

    if (error) {
      alert(error.message)
    } else {
      setPasswords({ newPassword: '', confirmPassword: '' })
      alert('Password updated successfully.')
    }

    setSavingPassword(false)
  }

  async function savePreferences() {
    if (!prefsStorageKey) return
    setSavingPrefs(true)
    localStorage.setItem(prefsStorageKey, JSON.stringify(preferences))
    setSavingPrefs(false)
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <p>Loading settings...</p>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background">
      <main className="max-w-3xl mx-auto p-6 space-y-6">
        <div className="flex items-center justify-between">
          <h1 className="text-3xl font-bold">Settings</h1>
          {profile?.role && (
            <Link href={`/settings/${profile.role}`}>
              <Button variant="outline">Role Settings</Button>
            </Link>
          )}
        </div>

        <Card>
          <CardHeader>
            <CardTitle>Profile</CardTitle>
            <CardDescription>Update your account profile.</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="name">Full Name</Label>
              <Input id="name" value={form.full_name} onChange={(e) => setForm({ ...form, full_name: e.target.value })} />
            </div>
            <div className="space-y-2">
              <Label htmlFor="email">Email</Label>
              <Input id="email" value={form.email} disabled />
            </div>
            <Button onClick={saveProfile} disabled={savingProfile}>{savingProfile ? 'Saving...' : 'Save Profile'}</Button>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Password</CardTitle>
            <CardDescription>Change password for this account.</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="newPassword">New Password</Label>
              <Input
                id="newPassword"
                type="password"
                value={passwords.newPassword}
                onChange={(e) => setPasswords({ ...passwords, newPassword: e.target.value })}
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="confirmPassword">Confirm Password</Label>
              <Input
                id="confirmPassword"
                type="password"
                value={passwords.confirmPassword}
                onChange={(e) => setPasswords({ ...passwords, confirmPassword: e.target.value })}
              />
            </div>
            <Button onClick={updatePassword} disabled={savingPassword}>{savingPassword ? 'Updating...' : 'Update Password'}</Button>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Preferences</CardTitle>
            <CardDescription>Control dashboard behavior and notifications.</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="flex items-center justify-between border rounded-md p-3">
              <div>
                <p className="font-medium">Email Updates</p>
                <p className="text-sm text-muted-foreground">Receive updates about activity and progress.</p>
              </div>
              <Switch checked={preferences.email_updates} onCheckedChange={(checked) => setPreferences({ ...preferences, email_updates: checked })} />
            </div>
            <div className="flex items-center justify-between border rounded-md p-3">
              <div>
                <p className="font-medium">Class Reminders</p>
                <p className="text-sm text-muted-foreground">Show reminder cues in dashboards.</p>
              </div>
              <Switch checked={preferences.class_reminders} onCheckedChange={(checked) => setPreferences({ ...preferences, class_reminders: checked })} />
            </div>
            <Button onClick={savePreferences} disabled={savingPrefs}>{savingPrefs ? 'Saving...' : 'Save Preferences'}</Button>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Role Context</CardTitle>
            <CardDescription>Role: {profile?.role || 'unknown'}</CardDescription>
          </CardHeader>
          <CardContent>
            {profile?.role === 'teacher' && <p>Teacher tools are available in `/protected/teacher` and role settings.</p>}
            {profile?.role === 'school_admin' && <p>School admin tools are available in `/school/admin` and role settings.</p>}
            {profile?.role === 'district_admin' && <p>District admin tools are available in `/district/admin` and role settings.</p>}
            {profile?.role === 'full_admin' && <p>Full admin tools are available in `/admin/panel` and role settings.</p>}
            {!['teacher', 'school_admin', 'district_admin', 'full_admin'].includes(profile?.role) && <p>General user settings only.</p>}
          </CardContent>
        </Card>
      </main>
    </div>
  )
}
